@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500&display=swap);
.DownloadPage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}
.PageFrame {
    margin: 0 auto;
    padding: 10px;
    width: 100%;
    max-width: 1200px;
}
.ContentArea {
    padding: 20px 10px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #e5e5e5;
    border-radius: 5px;
}
.ContentBox {
    margin: 10px;
    padding: 10px;
    background-color: #f3f4f5;
    border: 1px solid #e0e2e4;
    border-radius: 5px;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentSubtitle {
    margin-bottom: 20px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-bottom: 2px solid #000000;
    font-size: 16pt;
    font-family: 'Nanum Gothic', sans-serif;
}
.ContentTextArea {
    padding: 5px 10px;
    font-size: 12pt;
}

.ContentTextArea p {
    margin-bottom: 16px;
}
.ContentTitleBar {
    margin: 30px 0;
    padding: 30px;
    background-color: #ffffff;
    border: 1px solid #e7eaec;
    border-top: 3px solid #eea3aa;
    border-radius: 5px;
    font-size: 15pt;
}
.ContentSrcViewer {
    margin: 10px;
    padding: 15px;
    color: #f8f8f2;
    background-color: #272822;
    font-family: 'Source Code Pro', monospace;
    word-break: break-all;
}
.Versions {
    margin: 30px 0;
}
